/*estilos de la foto de perfil */

.grid {
    display: block;
    margin: 0 auto;                  
    width: 95%;
    padding-left: 0;  
    font-size: 0;  
    text-align: center;
  }
  
  .grid figure {
    margin: 0.1em;
    width: 100%;
    height: 50%;
    /* background: #3085a3; */
    display: inline-block;
    position: relative;
    overflow: hidden;
    text-align: center;
  }
  
  /* Common style */
  .grid figure img {
    position: relative;
    display: block;
    opacity: 0.8;
    height: 335px;
    width: 100%;
  
  }
  
  
  
  .grid figure figcaption {
    color: #F4D8E8;
    font-size: 1.25em;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .grid figcaption h2 {
    text-transform: uppercase;
    word-spacing: -0.15em;
    font-weight: 300;
    margin: 1em;
  }
  
  .grid figure figcaption::before,
  .grid figure figcaption::after {
    pointer-events: none;
  }
  
  .grid figure figcaption,
  .grid figure figcaption > a {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 90%;
  }
  
  .grid figure figcaption > a {
    z-index: 1000;
    text-indent: 200%;
    white-space: nowrap;
    font-size: 0;
    opacity: 0;
  }
  
  .grid figure h2 {
    word-spacing: -0.15em;
    font-weight: 300;
    margin: 1em;
  }
  
  .grid figure h2 span {
    font-weight: 800;
  }
  
  figure.effect-steve {
    z-index: auto;
    /* background: #000; */
  }
  
  figure.effect-steve:before {
    box-shadow: 0 3px 30px rgba(0, 0, 0, 0.8);
    opacity: 0;
  }
  
  figure.effect-steve figcaption {
    z-index: 1;
  }
  
  figure.effect-steve img {
    opacity: 1;
    -webkit-transition: -webkit-transform 0.35s;
    transition: transform 0.35s;
    -webkit-transform: perspective(1000px) translate3d(0, 0, 0);
    transform: perspective(1000px) translate3d(0, 0, 0);
  }
  
  figure.effect-steve h2,
  figure.effect-steve p {
    background: #F4D8E8;
    color: #2d434e;
  }
  
  figure.effect-steve h2 {
    position: absolute;
    padding: 0.25em;
    bottom: 10px;
    width: 100%;
  }
  
  figure.effect-steve p {
    margin-top: 1em;
    padding: 0.5em;
    width:100%;
    font-weight: 800;
    opacity: 0;
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
  }
  
  figure.effect-steve:hover:before {
    opacity: 1;
  }
  
  figure.effect-steve:hover img {
    -webkit-transform: perspective(1000px) translate3d(0, 0, 21px);
    transform: perspective(1000px) translate3d(0, 0, 21px);
  }
  
  figure.effect-steve:hover h2:before {
    opacity: 0;
  }
  
  figure.effect-steve:hover p {
    opacity: 1;
  }
  
  .grid figure,
  figcaption,
  img {
    z-index: 0;
  }
  
  .grid figure figcaption h2 {
    font-size: 24px;
    margin-bottom: 0;
    margin-left: 0;
    margin-right: 0;
  }
  
  .grid figcaption p {
    font-size: 16px;
    font-weight: 200;
    position: absolute;
    bottom: 0;
    margin: 0;
    height: 25%;
    width:100%;
  }
  
  figure:hover h2 {
    background: #f1f1f1;
    transition: transform 0.35s;
    transform: translateY(-2.5em);
  }